var vm = {
  name: "player",
  props: {
    show: {
      type: Boolean,
      default: false
    },
    src: {
      type: String,
      default: ""
    }
  },
  watch:{
    src(val){
      vm.$refs.player.src = val
      vm.$refs.player.load()
    }
  },
  data() {
    vm = this;
    return {};
  },
  created() {},
  mounted() {},
  methods: {
    close() {
      vm.$emit("close", false);
    }
  }
};
export default vm;
